
import './top.css';
import React from 'react';

import { withRouter } from 'react-router-dom'
class Top extends React.Component{


  state={
    navs:[
      {
        name:"",
        path:"/Content"
      },
      {
        name:"",
        path:"/Example"
      },
    ],
    actIdx:0
  };
  clickHandle=(idx)=>{
    let path = this.state.navs[idx].path;
    this.props.history.push(path);
    this.setState({
      actIdx:idx
  })

  }
  componentDidMount(){
    let href = document.location.href;
    let params = href.split("/");

    let curr = params[4];
    if(curr==="Example"){
      this.setState({
        actIdx:1
    })
    }
    else{
      this.setState({
        actIdx:0
    })
    }

    
  }
  render(){
    let navItems = [];
    this.state.navs.forEach((item,idx)=>{
      let navItem = <span key={idx} className={idx==this.state.actIdx?'act':''} onClick={()=>{this.clickHandle(idx)}}>{item.name}</span>;
      navItems.push(navItem);
    })
    return (
      <div className="top">
        <div className="right">
          {navItems}
        </div>
      </div>
    );
  }
  itemClick=(idx)=>{
    this.setState({
      actIdx:idx
  })

  }
  
}


export default withRouter(Top);